/* COLORS

Light - #DDDDDD
Medium - #666666
Dark - #444444

Accent - $color-secondary

*/

 /*********** GLOBAL ***********/

html, #wpwrap {
	background:#DDDDDD;
}

a {
	color:#000;
}

a:active,a:focus,a:hover {
	color:#666666;
}

#media-upload a.del-link:hover,.subsubsub a.current:hover,.subsubsub a:hover,div.dashboard-widget-submit input:hover {
	color:#666666;
}

input[type=checkbox]:checked:before {
	color:#666666;
}

input[type=radio]:checked:before {
	background:#666666;
}

.wp-core-ui input[type=reset]:active,.wp-core-ui input[type=reset]:hover {
	color:#666666;
}

.wp-core-ui .button-primary {
	background: $color-secondary;
	border-color:#b9b9b8 #b1b1b1 #b1b1b1;
	color:#fff;
	-webkit-box-shadow:none;
	box-shadow:none;
	text-shadow:none;
}

.wp-core-ui .button-primary:focus,.wp-core-ui .button-primary:hover {
	background:#444444;
	border-color:#b1b1b1;
	color:#fff;
	-webkit-box-shadow:none;
	box-shadow:none;
}

.wp-core-ui .button-primary:focus {
	-webkit-box-shadow:none;
	box-shadow:none;
}

.wp-core-ui .button-primary:active {
	background:#b9b9b8;
	border-color:#b1b1b1;
	-webkit-box-shadow:none;
	box-shadow:none;
}

.wp-core-ui .button-primary.button-primary-disabled,.wp-core-ui .button-primary.disabled,.wp-core-ui .button-primary:disabled,.wp-core-ui .button-primary[disabled] {
	color:#d1ccc7!important;
	background:#b9b9b9!important;
	border-color:#b1b1b1!important;
	text-shadow:none!important;
}

.wp-core-ui .button-primary.button-hero {
	-webkit-box-shadow:none!important;
	box-shadow:none!important;
}

.wp-core-ui .button-primary.button-hero:active {
	-webkit-box-shadow:none!important;
	box-shadow:none!important;
}

.wp-core-ui .wp-ui-primary {
	color:#fff;
	background-color:#666666;
}

.wp-core-ui .wp-ui-text-primary {
	color:#666666;
}

.wp-core-ui .wp-ui-highlight {
	color:#fff;
	background-color:$color-secondary;
}

.wp-core-ui .wp-ui-text-highlight {
	color:$color-secondary;
}

.wp-core-ui .wp-ui-notification {
	color:#fff;
	background-color:#222222;
}

.wp-core-ui .wp-ui-text-notification {
	color:#222222;
}

.wp-core-ui .wp-ui-text-icon {
	color:#f3f2f1;
}

.tablenav .tablenav-pages a:focus,.tablenav .tablenav-pages a:hover,.wrap .add-new-h2:hover,.wrap .page-title-action:hover {
	color:#fff;
	background-color:#666666;
}

.view-switch a.current:before {
	color:#666666;
}

.view-switch a:hover:before {
	color:#222222;
}

#adminmenu,#adminmenuback,#adminmenuwrap {
