	background:#444444;
}

#collapse-menu {
	color:#f3f2f1;
}

#collapse-menu:hover {
	color:#fff;
}

#collapse-button div:after {
	color:#f3f2f1;
}

#collapse-menu:hover #collapse-button div:after {
	color:#fff;
}

#wpadminbar {
	color:#fff;
	background:#666666;
}

#wpadminbar .ab-item,#wpadminbar a.ab-item,#wpadminbar>#wp-toolbar span.ab-label,#wpadminbar>#wp-toolbar span.noticon {
