	background:#6c645c;
}

#wpadminbar #adminbarsearch .adminbar-input::-webkit-input-placeholder {
	color:#fff;
	opacity:.7;
}

#wpadminbar #adminbarsearch .adminbar-input:-moz-placeholder {
	color:#fff;
	opacity:.7;
}

#wpadminbar #adminbarsearch .adminbar-input::-moz-placeholder {
	color:#fff;
	opacity:.7;
}

